body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #B5A292; 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{
  display: flex;
  flex-wrap: wrap;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}
ul{
  /*remove bullets from list items*/
  list-style-type: none; 
  
}

/* nav element spacing through li margins  */
li{
  margin-right: 80px;
  margin-left: 80px;
  color: white;
}


a:hover {
  text-decoration: underline;
}

/* project thumbnails styling -- bootstrap implementation would be a good look*/

.projects-box{
display: flex;
flex-direction: row;
flex-wrap: wrap;
justify-content: space-around;
width: 1000px;
height: 1000px;

}

.project{
  height: 200px;
  width: 250px;
  margin: 10px;
  padding: 20px;
  border: #FFF3D9 5px solid;
  border-radius: 15%;
  
}

div.project:hover{
  opacity: .7;
  
 
 
}




.profile-image{
  height: 300px;
  width: 400px;
  float: left;
  margin-right: 15px;
  border: #FFF3D9 5px solid;
  
}

#resume{
  height: 800px;
  width: 600px;
}


/*projects' background images */

#project1{
  background-image: url(assets/0.jpeg) ;
  background-color: black ;
  background-size: cover;
  height: 200px;

}

