.form-field .message-field{
  margin: 5px;
  width: 200px;
  color: #FFF3D9;
}

.message-field{
  width: 200px;
  
}

.component-header{
  color: #FFF3D9;
  top: 500px;
}


.subheader{
  font-size: 15px;
}

.project-description{
  font-size: small;
  color: #948155;
  background-color: white;
}

.about-description{
  color: #FFF3D9;
}

.form-labels{
  color: #FFF3D9;
}

.code-snippet-body{
 background-color: #FFF3D9;
}

.code-snippet-title{
 font-size: 15px !important;
}

.header-element{
  background-color: #282c34;
}


.about-description{
  width: 800px;
  margin: 80px;
 
}

.fullstack-list-item{
  color: black;
}

.programmer-list-item{
  color: black; 
}

/* contact component */
.contact-header{
  color: #FFF3D9;
  
}

.contact-page-div{
  background-size: cover;  

}

/* footer */ 

.footer{
  background-color: #282c34; 
}


/* Media Query Section for Responsiveness */

@media only screen and (max-width: 600px) {
 /*resume component */
  #resume{
    width: 500px;

  }

  /*project component */
  .project{
   margin: 0px;
  }

  .code-snippet-image{
    width: 100px;
    height: 400px;
    margin: 8px;
    

  }
  .card-group{
    width: 400px !important ;
  
    
  }
  .code-snippet-card{
    width: 380px;
    
  }

  /*about component */


  .about-description{
    width: 200px;
    margin: 80px;
   
  }

  .profile-image{
    width: 200px;
    height:250px;
  }

}

/* contact page component */

.contact-page-div{
  width: 100vw; 
}